html {
  position: relative;
  box-sizing: border-box;
}

:root {
  --main-color: #FFFFFF;
  --secondary-color: #2D2D2D;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  scroll-behavior: smooth;
}

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: "Lato", sans-serif;
  background-color: #FFFFFF;
  color: #FFFFFF;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3;
}

img, svg {
  max-width: 100%;
  max-height: 100%;
  height: auto;
}

button {
  padding: 0;
  cursor: pointer;
  color: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p,
figure,
fieldset {
  margin: 0;
}

fieldset {
  border: none;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.container {
  max-width: 1830px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (max-width: 1240px) {
  .container {
    max-width: 1240px;
  }
}
@media (max-width: 850px) {
  .container {
    max-width: 850px;
  }
}
@media (max-width: 600px) {
  .container {
    max-width: 600px;
    padding: 0 20px;
  }
}

@-ms-viewport {
  width: device-width;
}
.header {
  padding: 26px 0 45px;
}
@media (max-width: 1240px) {
  .header {
    padding: 34px 0 30px;
  }
}
@media (max-width: 850px) {
  .header {
    padding: 25px 0;
  }
}
@media (max-width: 600px) {
  .header {
    padding: 10px 0;
  }
}
.header__container {
  display: grid;
  grid-template-columns: minmax(150px, 212px) minmax(140px, 155px) minmax(200px, 757px) minmax(150px, 354px);
  grid-template-areas: "logo menu-button search user-info";
  gap: 97px;
  align-items: center;
}
@media (max-width: 1500px) {
  .header__container {
    gap: 30px;
  }
}
@media (max-width: 1240px) {
  .header__container {
    grid-template-columns: max-content 1fr;
    grid-template-rows: auto auto;
    grid-template-areas: "logo user-info" "menu-button search";
    gap: 34px 50px;
  }
}
@media (max-width: 850px) {
  .header__container {
    grid-template-columns: 150px 1fr;
    gap: 29px 0;
  }
}
@media (max-width: 600px) {
  .header__container {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto auto;
    grid-template-areas: "logo ." "menu-button user-info" "search search";
    gap: 20px;
    max-width: 100%;
  }
}
.header__logo {
  grid-area: logo;
  display: block;
  justify-self: start;
  padding-top: 14px;
}
@media (max-width: 1240px) {
  .header__logo {
    padding-top: 8px;
  }
}
@media (max-width: 850px) {
  .header__logo {
    max-width: 165px;
    width: 165px;
    margin-right: -15px;
  }
}
@media (max-width: 600px) {
  .header__logo {
    width: 114px;
  }
}
@media (max-width: 850px) {
  .header__logo svg {
    width: 165px;
    height: auto;
  }
}
.header__menu-button {
  grid-area: menu-button;
  justify-self: center;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 22px;
  line-height: 150%;
  border-radius: 10px;
  padding: 11px 28px 10px 61px;
  background-image: url("../imgs/menu-open.svg");
  background-repeat: no-repeat;
  background-position: center left 30px;
  background-color: #3670C7;
  border: none;
  outline: 0;
  transition: background-color 0.2s ease-out;
}
.header__menu-button:hover, .header__menu-button:active {
  background-color: #5D92E0;
}
.header__menu-button:focus-visible {
  background-color: #3B65A5;
}
@media (max-width: 1500px) {
  .header__menu-button {
    justify-self: start;
  }
}
@media (max-width: 1240px) {
  .header__menu-button {
    padding: 11px 30px 10px 61px;
    background-position: center left 33px;
  }
}
@media (max-width: 850px) {
  .header__menu-button {
    border-radius: 6px;
    font-size: 16px;
    line-height: 1.3;
    padding: 11px 21px 10px 49px;
    background-position: center left 24px;
    background-size: 18px;
  }
}
@media (max-width: 600px) {
  .header__menu-button {
    border-radius: 4px;
    font-size: 12px;
    padding: 8px 15px 7px 31px;
    background-position: center left 17px;
    background-size: 11px;
  }
}
.header__search {
  grid-area: search;
  background: #EFEFEF;
  border-radius: 30px;
  display: grid;
  grid-template-columns: auto 101px;
  align-items: center;
}
@media (max-width: 850px) {
  .header__search {
    grid-template-columns: auto 82px;
  }
}
@media (max-width: 600px) {
  .header__search {
    grid-template-columns: 1fr 50px;
  }
}
.header__user {
  grid-area: user-info;
  max-width: 354px;
  font-size: 16px;
  line-height: 130%;
  color: #2D2D2D;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 70px;
  justify-items: start;
  align-items: end;
}
@media (max-width: 1500px) {
  .header__user {
    gap: 40px;
  }
}
@media (max-width: 1240px) {
  .header__user {
    max-width: 294px;
    justify-self: end;
  }
}
@media (max-width: 850px) {
  .header__user {
    max-width: 221px;
    font-size: 12px;
    gap: 30px;
    justify-self: end;
    line-height: 1;
  }
}
@media (max-width: 600px) {
  .header__user {
    max-width: 100%;
    gap: 20px;
    justify-self: end;
  }
}

.search__input {
  font-size: 16px;
  line-height: 1.3;
  color: #2D2D2D;
  background: inherit;
  border: 0;
  border-radius: 30px;
  padding: 15px 10px 18px 40px;
  max-width: 100%;
}
@media (max-width: 850px) {
  .search__input {
    font-size: 12px;
    line-height: 1.35;
    padding: 13px 10px 13px 30px;
  }
}
@media (max-width: 600px) {
  .search__input {
    font-size: 10px;
    line-height: 1.3;
    padding: 9px 10px 10px 16px;
  }
}
.search__input:focus-visible {
  outline: solid var(--4, #8F8F8F);
}
.search__input::placeholder {
  font-size: 16px;
  line-height: 1.3;
  color: #8F8F8F;
}
@media (max-width: 850px) {
  .search__input::placeholder {
    font-size: 12px;
  }
}
@media (max-width: 600px) {
  .search__input::placeholder {
    font-size: 10px;
  }
}
.search__button {
  color: #FFFFFF;
  background: url("../imgs/search.svg") no-repeat center #3670C7;
  border-radius: 30px;
  height: 100%;
  border: 0;
  outline: 0;
  transition: background-color 0.2s ease-out;
}
.search__button:hover, .search__button:active {
  background-color: #5D92E0;
}
.search__button:focus-visible {
  background-color: #3B65A5;
}
@media (max-width: 850px) {
  .search__button {
    background-size: 22px;
  }
}
@media (max-width: 600px) {
  .search__button {
    background-size: 15px;
  }
}

.user__link {
  display: block;
}
.user__link::before {
  content: "";
  display: block;
  width: 36px;
  height: 36px;
  margin: 0 auto 4px;
  background: no-repeat center/auto;
}
@media (max-width: 1240px) {
  .user__link::before {
    margin: 0 auto 5px;
  }
}
@media (max-width: 850px) {
  .user__link::before {
    width: 30px;
    height: 30px;
    margin: 0 auto 2px;
    background-size: 25px;
  }
}
.user__link_type_profile::before {
  background-image: url("../imgs/profile.svg");
}
@media (max-width: 850px) {
  .user__link_type_profile::before {
    background-size: 20px;
  }
}
.user__link_type_basket {
  position: relative;
}
.user__link_type_basket::before {
  background-image: url("../imgs/shopping-cart.svg");
}
@media (max-width: 850px) {
  .user__link_type_basket::before {
    background-size: 29px;
  }
}
.user__link_type_favorite::before {
  background-image: url("../imgs/favorite.svg");
}
@media (max-width: 850px) {
  .user__link_type_favorite::before {
    background-size: 25px;
  }
}
@media (max-width: 600px) {
  .user__text {
    display: none;
  }
}
.user__basket-count {
  display: block;
  font-size: 14px;
  line-height: 1;
  background-color: #FFFFFF;
  border-radius: 50%;
  color: #3670C7;
  text-align: center;
  position: absolute;
  width: 14px;
  height: 14px;
  top: 0;
  right: 8px;
}
@media (max-width: 600px) {
  .user__basket-count {
    right: 0;
  }
}

.modal {
  background-color: #FFFFFF;
  position: absolute;
  width: 100%;
  top: 107px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 22px;
  font-weight: 400;
  line-height: 150%;
  color: #2D2D2D;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s ease-out;
}
@media (max-width: 1240px) {
  .modal {
    top: 190px;
  }
}
@media (max-width: 850px) {
  .modal {
    top: 150px;
    font-size: 16px;
  }
}
@media (max-width: 600px) {
  .modal {
    font-size: 12px;
  }
}
.modal.show-modal {
  visibility: visible;
  opacity: 1;
  transition: all 0.2s ease-out;
}
.modal__container {
  padding: 52px 30px 92px;
  display: flex;
  align-items: flex-start;
  flex-flow: row wrap;
  gap: 50px 225px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.2);
}
@media (max-width: 1500px) {
  .modal__container {
    gap: 50px 150px;
  }
}
@media (max-width: 1240px) {
  .modal__container {
    gap: 50px 100px;
  }
}
@media (max-width: 850px) {
  .modal__container {
    padding: 30px;
    gap: 50px;
  }
}
@media (max-width: 600px) {
  .modal__container {
    padding: 20px 20px 30px;
    gap: 30px;
  }
}
.modal__title {
  font-size: 32px;
  line-height: 130%;
  margin-bottom: 40px;
}
@media (max-width: 850px) {
  .modal__title {
    font-size: 26px;
    margin-bottom: 20px;
  }
}
@media (max-width: 600px) {
  .modal__title {
    font-size: 20px;
    margin-bottom: 10px;
  }
}
.modal__list_catalog {
  display: grid;
  grid-template-columns: repeat(2, 225px);
  grid-template-rows: repeat(5, max-content);
  grid-auto-flow: column;
  column-gap: 83px;
}
@media (max-width: 1240px) {
  .modal__list_catalog {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(10, max-content);
    column-gap: 0;
  }
}
.modal__item {
  margin-bottom: 20px;
}
.modal__item:hover {
  color: #3670C7;
}
@media (max-width: 850px) {
  .modal__item {
    margin-bottom: 10px;
  }
}
@media (max-width: 600px) {
  .modal__item {
    font-size: 12px;
    line-height: 120%;
  }
}
.modal__item:last-child {
  margin-bottom: 0;
}

