.checkbox-label {
  position: relative;
  display: block;
  cursor: pointer;
  padding-left: 30px;
}

.checkbox-input {
  position: absolute;
  appearance: none;
  cursor: pointer;
  margin-left: -30px;
}
.checkbox-input::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 20px;
  height: 20px;
  background-color: #3670C7;
  border-radius: 2px;
  transition: all 0.2s ease-out;
}
.checkbox-input:after {
  content: "";
  position: absolute;
  top: 6px;
  left: 5px;
  width: 10px;
  height: 6px;
  border: 3px solid #FFFFFF;
  border-top: none;
  border-right: none;
  opacity: 0;
  transform: rotate(-45deg);
}
@media (max-width: 600px) {
  .checkbox-input:after {
    border-width: 2px;
  }
}
.checkbox-input:focus-visible:before {
  outline: none;
  background-color: #3B65A5;
}
.checkbox-input:checked::after {
  opacity: 1;
}
.checkbox-input:hover:before {
  background-color: #5D92E0;
}

