.delivery__address {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 15px;
}
.delivery__address-title {
  flex: 1 1 266px;
  color: #8F8F8F;
  font-size: 16px;
  font-weight: 400;
}
@media (max-width: 850px) {
  .delivery__address-title {
    display: none;
  }
}
.delivery__address-text {
  flex: 0 1 calc(100% - 266px);
}
@media (max-width: 850px) {
  .delivery__address-text {
    font-size: 14px;
    flex-basis: 100%;
  }
}
@media (max-width: 600px) {
  .delivery__address-text {
    font-size: 10px;
  }
}
.delivery__price {
  justify-content: flex-start;
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 32px;
}
@media (max-width: 850px) {
  .delivery__price {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #E8E8E8;
  }
}
@media (max-width: 600px) {
  .delivery__price {
    padding-bottom: 15px;
    margin-bottom: 15px;
  }
}
.delivery__price-title {
  flex: 1 1 266px;
  color: #8F8F8F;
  font-size: 16px;
  font-weight: 400;
}
@media (max-width: 850px) {
  .delivery__price-title {
    display: none;
  }
}
.delivery__price-text {
  flex: 0 1 calc(100% - 266px);
}
@media (max-width: 850px) {
  .delivery__price-text {
    font-size: 14px;
    flex-basis: 100%;
  }
}
@media (max-width: 600px) {
  .delivery__price-text {
    font-size: 12px;
    color: #8F8F8F;
  }
}
@media (max-width: 850px) {
  .delivery__wrap {
    flex-direction: column;
    align-items: flex-start;
  }
}
.delivery__date {
  flex: 0 1 266px;
  font-size: 16px;
}
@media (max-width: 850px) {
  .delivery__date {
    margin-bottom: 16px;
    flex-basis: content;
  }
}
@media (max-width: 600px) {
  .delivery__date {
    font-size: 12px;
    margin-bottom: 10px;
  }
}
.delivery__list {
  flex: 0 1 calc(100% - 266px);
  flex-flow: row wrap;
  gap: 17px;
}
@media (max-width: 850px) {
  .delivery__list {
    gap: 15px;
    flex-basis: auto;
  }
}
.delivery__item-link {
  display: block;
  border-radius: 10px;
  border: 1px solid #E8E8E8;
  overflow: hidden;
  font-size: 0;
  flex: 0 0 80px;
}
@media (max-width: 850px) {
  .delivery__item-link {
    flex: 0 0 60px;
  }
}
.delivery__item-img {
  width: 80px;
  height: 80px;
  object-fit: contain;
  object-position: center;
}
@media (max-width: 850px) {
  .delivery__item-img {
    width: 60px;
    height: 60px;
  }
}

.total__header {
  margin-bottom: 20px;
  font-size: 26px;
}
@media (max-width: 850px) {
  .total__header {
    font-size: 22px;
  }
}
@media (max-width: 600px) {
  .total__header {
    font-size: 16px;
  }
}
.total__data {
  color: #8F8F8F;
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 10px;
}
@media (max-width: 850px) {
  .total__data {
    font-size: 12px;
    margin-bottom: 15px;
  }
}
@media (max-width: 600px) {
  .total__data {
    font-size: 10px;
    margin-bottom: 10px;
  }
}
.total__data_mb20 {
  margin-bottom: 20px;
}
@media (max-width: 600px) {
  .total__data_mb20 {
    margin-bottom: 15px;
  }
}
.total__delivery {
  gap: 5px;
  font-size: 16px;
  margin-bottom: 10px;
}
@media (max-width: 850px) {
  .total__delivery {
    justify-content: space-between;
  }
}
.total__delivery_mb20 {
  margin-bottom: 20px;
}
@media (max-width: 600px) {
  .total__delivery_mb20 {
    margin-bottom: 15px;
  }
}
.total__delivery_mb30 {
  margin-bottom: 30px;
}
@media (max-width: 850px) {
  .total__delivery_mb30 {
    margin-bottom: 40px;
  }
}
@media (max-width: 600px) {
  .total__delivery_mb30 {
    margin-bottom: 20px;
  }
}
.total__delivery-text {
  color: #3670C7;
  font-weight: 700;
  border: 0;
  background-color: transparent;
}
.total__delivery-address {
  color: #8F8F8F;
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 20px;
}
@media (max-width: 850px) {
  .total__delivery-address {
    font-size: 12px;
  }
}
@media (max-width: 600px) {
  .total__delivery-address {
    font-size: 10px;
    margin-bottom: 15px;
  }
}
.total__delivery-address-text:not(:last-child) {
  margin-bottom: 5px;
}
@media (max-width: 850px) {
  .total__delivery-address-text:not(:last-child) {
    margin-bottom: 10px;
  }
}
.total__order-btn {
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 700;
  border-radius: 10px;
  padding: 16px;
  text-align: center;
  min-width: 266px;
  background-color: #3670C7;
  border: none;
  outline: 0;
  transition: background-color 0.2s ease-out;
  display: block;
  margin: 0 auto 20px;
}
.total__order-btn:hover, .total__order-btn:active {
  background-color: #5D92E0;
}
.total__order-btn:focus-visible {
  background-color: #3B65A5;
}
@media (max-width: 850px) {
  .total__order-btn {
    width: 100%;
    margin: 0 0 30px 0;
  }
}
@media (max-width: 600px) {
  .total__order-btn {
    margin: 0 0 20px 0;
  }
}
.total__terms {
  font-size: 16px;
  font-weight: 400;
}
@media (max-width: 850px) {
  .total__terms {
    font-size: 12px;
  }
}
@media (max-width: 600px) {
  .total__terms {
    font-size: 10px;
  }
}
.total__terms-checkbox-input {
  margin-top: 5px;
}
@media (max-width: 850px) {
  .total__terms-checkbox-input {
    margin-top: 0;
  }
}
.total__terms-text {
  display: inline-block;
  color: #8F8F8F;
}
@media (max-width: 850px) {
  .total__terms-text {
    padding-top: 4px;
  }
}
@media (max-width: 600px) {
  .total__terms-text {
    padding-top: 0;
  }
}
.total__terms-text_dark {
  color: #525252;
}

.category {
  padding: 45px 0 96px;
  color: #2D2D2D;
}
@media (max-width: 1240px) {
  .category {
    padding: 30px 0 88px;
  }
}
@media (max-width: 850px) {
  .category {
    padding: 25px 0 57px;
  }
}
@media (max-width: 600px) {
  .category {
    padding: 30px 0 100px;
  }
}
.category__breadcrumbs {
  margin-bottom: 60px;
}
@media (max-width: 1240px) {
  .category__breadcrumbs {
    margin-bottom: 50px;
  }
}
@media (max-width: 850px) {
  .category__breadcrumbs {
    margin-bottom: 40px;
  }
}
@media (max-width: 600px) {
  .category__breadcrumbs {
    margin-bottom: 20px;
  }
}
.category__title {
  font-size: 50px;
  margin-bottom: 50px;
}
@media (max-width: 1240px) {
  .category__title {
    font-size: 30px;
    margin-bottom: 40px;
  }
}
@media (max-width: 850px) {
  .category__title {
    margin-bottom: 30px;
  }
}
@media (max-width: 600px) {
  .category__title {
    font-size: 20px;
    margin-bottom: 25px;
  }
}
.category__warning {
  font-size: 26px;
  color: #F00;
}